  .go-top {
    position: fixed;
    bottom: 48px;
    right: 24px;
    height: 50px;
    width: 50px;
    display: none;
    place-items: center;
    border-radius: 100%;
    box-shadow: var(--box-shadow);
    z-index: 1001;
    cursor: pointer;
  }
  
  .go-top .progress-value {
    height: calc(100% - 6px);
    width: calc(100% - 6px);
    background-color: rgba(var(--white), 1);
    border-radius: 50%;
    display: grid;
    font-size: 24px;
    color: rgba(var(--dark), 1);
    padding: 5px;
  }
  
  .go-top .progress-value .ti:before {
    margin-left: 5px;
    line-height: 1.3;
  }
  
  .txt-ellipsis {
    max-width: calc(100% - 80px);
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  
