  /* Drag and drop css */
  .list-event {
    padding: 0.8rem 0.75rem;
    padding-left: 15px;
    cursor: all-scroll;
    margin: 10px 0;
    border-radius: var(--app-border-radius);
    font-size: 16px;
    white-space: nowrap;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  .event-container {
    width: 100%;
    height: 350px;
    overflow: hidden;
  }
  
  .event-container .event-box {
    border: 1px solid rgba(var(--dark), 0.2);
    padding: 0.8rem 0.75rem;
    border-radius: 10px;
    margin-bottom: 10px;
    border-left: 4px solid rgba(var(--primary), 1);
    background-color: rgba(var(--white), 0.3);
  }
  
  .event-container .event-box p {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
  
  .event-container .event-box h6 {
    color: rgba(var(--dark), 0.75);
    font-weight: 600;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    -size: calc(14px + 2 * (100vw - 300px) / 1620);
  }
  
  .event-container .slick-active.slick-center {
    background-color: rgba(var(--primary), 0.3);
    box-shadow: var(--box-shadow);
    border-left: 4px solid rgba(var(--primary-dark), 1);
  }
  
  .event-container .slick-active.slick-center h6 {
    color: rgba(var(--primary-dark), 1);
  }
  
  .event-container .slick-active.slick-center .text-end {
    color: rgba(var(--primary-dark), 1);
  }
  
