  .check-box {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
  }
  
  .check-box input {
    display: none;
  }
  
  .check-box input:checked~.radiomark:before,
  .check-box input:checked~.checkmark:before {
    transform: translate(-50%, -50%) scale(1);
  }
  
  .check-box .checkmark {
    border-radius: 2px;
  }
  
  .check-box .checkmark,
  .check-box .radiomark {
    position: relative;
    width: 1.25rem;
    height: 1.25rem;
    margin-right: 0.6rem;
  }
  
  .check-box .checkmark:before,
  .check-box .radiomark:before {
    position: absolute;
    transition: all 0.2s ease-in-out;
  }
  
  .check-box .radiomark {
    border-radius: 50%;
  }
  
  .check-box .radiomark:before {
    content: "";
    width: 0.5rem;
    height: 0.5rem;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
  }
  
  .check-box .checkmark:before {
    font-family: "tabler-icons";
    content: "\ea5e";
    font-size: 18px;
    font-weight: 600;
    line-height: 18px;
    top: 9px;
    left: 13px;
    transform: translate(-50%, -50%) scale(0);
  }
  
  .form-checkimage {
    cursor: pointer;
    margin-bottom: 10px;
  }
  
  .form-checkimage .checkimage-input {
    z-index: -1;
    opacity: 0;
  }
  
  .form-checkimage .checkimage-input:checked~.check-box:before {
    font-family: "tabler-icons" !important;
    content: "\ea5e";
    background-color: rgb(var(--primary), 1);
    font-size: 16px;
    padding-left: 2px;
    line-height: 18px;
    font-weight: 600;
  }
  
  .form-checkimage .checkimage-input:checked~.check-box.radiobox:before {
    font-family: "tabler-icons" !important;
    content: "\f671";
    font-size: 10px;
    text-align: center;
    line-height: 20px;
    padding-left: 4px;
  }
  
  .form-checkimage .checkimage-input:checked~.check-box .checkbox-image {
    filter: blur(0px);
