  .form-checkimage .check-box:before {
    content: "";
    position: absolute;
    top: 10px;
    left: 15px;
    z-index: 1;
    color: rgba(var(--white), 1);
    width: 20px;
    height: 20px;
    border-radius: 2px;
    background-color: rgba(var(--white), 1);
