  .wraper {
    width: 100%;
    height: auto;
    border-radius: var(--app-border-radius);
    overflow: hidden;
    position: relative;
  }
  
  .wraper img {
    width: 100%;
    display: block;
    transition: all 0.5s;
  }
  
  .wraper:before {
    content: "";
    width: 100%;
    height: 0;
    position: absolute;
    bottom: 0;
    border-radius: 10px;
    background-color: rgb(var(--dark), 0.5);
    z-index: 1;
  }
  
  .wraper:hover img {
    transform: scale(1.2);
    overflow: hidden;
  }
  
  .wraper:hover:before {
    height: 100%;
  }
  
  .content-overlay {
    width: 100%;
    height: auto;
    border-radius: var(--app-border-radius);
    overflow: hidden;
    position: relative;
  }
  
  .content-overlay img {
    width: 100%;
    display: block;
    transition: all 0.5s;
  }
  
  .content-overlay:hover img {
    transform: scale(1.2);
    overflow: hidden;
  }
  
  .content-overlay:before {
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgb(var(--dark), 0.6);
    position: absolute;
    transform-origin: left;
    transform: scaleX(0);
    transition: all 0.5s cubic-bezier(0.66, 0.08, 0.19, 0.97);
    z-index: 1;
  }
  
  .content-overlay:hover:before {
    transform: scaleX(1);
  }
  
  .content-overlay:hover .content-overlay-text {
    opacity: 1;
    visibility: visible;
    bottom: 61px;
    left: 20px;
    right: 20px;
    color: rgba(var(--white), 1);
  }
  
  .content-overlay .content-overlay-text {
    position: absolute;
    left: 10px;
    right: 0;
    bottom: 0;
    line-height: 1.5;
    transition: all ease-in-out 0.5s;
    visibility: hidden;
    opacity: 0;
    z-index: 9999;
  }
  
  .content-overlay h5 {
    color: rgba(var(--white), 1) !important;
