  }
  
  .content-overlay.content-overlay-top:hover:before {
    transform: scaleY(1);
  }
  
  .content-overlay.content-overlay-bottom:before {
    transform-origin: bottom;
    transform: scaleY(0);
  }
  
  .content-overlay.content-overlay-bottom:hover:before {
    transform: scaleY(1);
  }
  
