  /* range */
  .slider-container {
    width: 100%;
    padding: 20px 0;
    position: relative;
  }
  
  .slider-wrapper {
    position: relative;
    width: 100%;
  }
  
  .range-slider {
    width: 100%;
    -webkit-appearance: none;
    height: 8px;
    border-radius: 5px;
    background: #ddd;
    outline: none;
  }
  
  .range-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 16px;
    height: 16px;
    background: #007bff;
    border-radius: 50%;
    cursor: pointer;
    position: relative;
    z-index: 2;
  }
  
  .slider-tooltip {
    position: absolute;
    top: -30px;
    background: rgba(var(--primary), .3) !important;
    color: var(--font-color);
    padding: 3px 8px;
    font-size: 14px;
    border-radius: 4px;
    transform: translateX(-50%);
    white-space: nowrap;
    z-index: 1;
    pointer-events: none;
  }
  
  .range-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 20px;
    height: 20px;         
    background: #000;      
    border-radius: 50%;     
    cursor: pointer;
    position: relative;
    z-index: 2;
    border: 2px solid #fff;   
  }
  
  .range-slider::-moz-range-thumb {
    width: 20px;             
    height: 20px;
    background: #000;
    border-radius: 50%;
    cursor: pointer;
    border: 2px solid #fff;
  }
  
