.border-selected {
  border: 2px solid rgb(var(--primary)) !important;
  box-shadow: 0 0 15px rgba(0, 123, 255, 0.25);
  border-radius: 10px;
}

.border-unselected {
  border: 1px solid #e0e0e0;
  border-radius: 10px;
}





  @keyframes spin {
    to {
      transform: rotate(360deg);
    }
  }
  
  
  @media (max-width: 992px) {
    #summaryCard {
      transition: top 0.3s ease;
    }
  }
  
  
  
  
  
  @media screen and (max-width: 1199px) and (min-width: 767px) {
    .rtl .app-wrapper nav.horizontal-sidebar .app-nav .main-nav>li:not(.menu-title) a::after {
      right: 0;
      left: auto;
    }
  }
  
  @media screen and (max-width: 1400px) {
    .rtl .app-wrapper .horizontal-sidebar~.app-content {
      padding-left: 0 !important;
      padding-right: 0 !important;
    }
  
    .rtl .horizontal-sidebar~.app-content .header-main .header-cloud-canvas {
      left: 550px !important;
      right: auto;
    }
  
    .rtl .horizontal-sidebar~.app-content .header-main .header-searchbar .offcanvas {
      left: 500px !important;
      right: auto;
    }
  
    .rtl .horizontal-sidebar~.app-content .header-main .header-apps .offcanvas {
      left: 450px !important;
      right: auto;
    }
  
    .rtl .horizontal-sidebar~.app-content .header-main .header-cart .offcanvas {
      left: 430px !important;
      right: auto;
    }
  
    .rtl .horizontal-sidebar~.app-content .header-main .header-notification .offcanvas {
      left: 350px !important;
      right: auto;
    }
  
    .rtl .horizontal-sidebar~.app-content .header-main .header-profile .offcanvas {
      left: 300px !important;
      right: auto;
    }
  }
  
  @media screen and (max-width: 1199px) {
    .rtl .app-wrapper .app-content {
      padding-top: 90px;
      padding-right: 0;
    }
  
    .rtl header.header-main {
      padding-right: 0;
      padding-left: 0 !important;
    }
  
    .rtl .toggle-semi-nav {
      float: left;
    }
  
    .rtl div>footer {
      padding-right: 4.5rem;
    }
  }
  
  @media screen and (max-width: 767px) {
    .rtl .app-wrapper .app-content {
      padding-right: 0 !important;
    }
  
    .rtl header.header-main {
      padding-right: 0 !important;
    }
  
    .rtl div>footer {
      padding-right: 0;
    }
  
    .rtl .footer-text {
      text-align: right;
    }
    .my-cpu-tab .nav-item{
      width: 100%;
    }
    .equal-card-memberlist{
      padding: 20px 0;
    }
    .merge-title{
      flex-direction: column;}
  }
  
  @media screen and (max-width: 1399px) and (min-width: 992px) {
    .order--1-lg {
      order: -1;
    }
  
    .order--2-lg {
      order: -2;
    }
  
    .order--3-lg {
      order: -3;
    }
  
    .order-1-lg {
      order: 1;
    }
  }
  
  @media screen and (max-width: 1399px) and (min-width: 768px) {
    .order--1-md {
      order: -1;
    }
  
    .order-1-md {
      order: 1;
    }
  }